.hide {
  //to hide element from flow
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  clip: rect(0 0 0 0);
  overflow: hidden;
}

.is-hidden {
  //to hide modals
  pointer-events: none;
  opacity: 0;
}

.--notScrolled {
  overflow: hidden;
}
// .gallery__img,
// .gallery__date,
.card-box {
  pointer-events: none;
}
