.page-header__thumb {
  padding-top: 80px;
  padding-bottom: 112px;
  background-image: url('/src/images/tablet/header-bg-tablet(1x).png');
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;


  @include bg-adapt-2x {
    background-image: url('/src/images/tablet/header-bg-tablet(2x).png');
  }

  @include bg-adapt-3x {
    background-image: url('/src/images/tablet/header-bg-tablet(3x).png');
  }

  @include desktop {
    background-image: url('/src/images/desktop/header-bg-desktop(1x).png');
  }

  @media screen and (min-width: 1280px) and (min-device-pixel-ratio: 2),
    (min-width: 1280px) and (-webkit-min-device-pixel-ratio: 2),
    (min-width: 1280px) and (min-resolution: 192dpi),
    (min-width: 1280px) and (min-resolution: 2dppx) {
    background-image: url('/src/images//desktop/header-bg-desktop(2x).png');
  }

  @media screen and (min-width: 1280px) and (min-device-pixel-ratio: 3),
    (min-width: 1280px) and (-webkit-min-device-pixel-ratio: 3),
    (min-width: 1280px) and (min-resolution: 288dpi),
    (min-width: 1280px) and (min-resolution: 3dppx) {
    background-image: url('/src/images/desktop/header-bg-desktop(3x).png');
  }
}

.background-image__top {
  position: absolute;
  top: 0;
  left: 0;

  width: 107px;
  height: 89px;

  background: linear-gradient(
    136.07deg,
    rgba(196, 196, 196, 0.03) 10.75%,
    rgba(196, 196, 196, 0) 92.33%
  );
  backdrop-filter: blur(15px);
  border-radius: 0 0 100px 0;
}

.logo {
  position: relative;
  z-index: 1;

  display: inline-block;
  width: 60px;
  height: 25px;

  top: 19px;
  left: 20px;
}

.logo__icon {
  fill: getColor('font-clr2');

  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  .logo:hover > &,
  .logo:focus > & {
    fill: getColor('font-clr1');
  }
}

.theme-box {
  position: absolute;
  top: 16px;
  right: 20px;
}

.change-theme-button {
  display: flex;
  padding: 5px;
  background: linear-gradient(136.07deg, rgba(196, 196, 196, 0.03) 10.75%,
                rgba(196, 196, 196, 0) 92.33%);
  backdrop-filter: blur(15px);
  border-radius: 50%;
  border: 0;
  outline: none;
  cursor: pointer;

  transition-property: box-shadow;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    box-shadow: 0px 0px 42px 4px rgba(220, 86, 197, 0.54);
  }
}

.light-theme-icon,
.dark-theme-icon {
  fill: getColor('font-clr2');
  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    

  .change-theme-button:hover>&,
  .change-theme-button:focus>& {
    fill: getColor('font-clr1');
  }
}

.page-title {
  @include title;
  max-width: 250px;
  margin-bottom: 24px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}

.header-form {
  @include flex-center;
  flex-direction: column;
  gap: 24px;
}

.header-form__thumb {
  position: relative;
}

.header-form__input {
  width: 100%;
  max-width: 280px;
  height: 40px;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  font-size: 16px;
  line-height: 1.25;

  padding: 10px 64px 10px 32px;
  color: getColor('font-clr2');

  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  border: 1px solid transparent;
  outline: none;

  transition-property: border, box-shadow;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:focus {
    border: 1px solid getColor('font-clr1');
    box-shadow: 0px 0px 42px 4px rgba(220, 86, 197, 0.54);
  }
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #939393;
  opacity: 1;
  /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #939393;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #939393;
}

.header-form__search-btn {
  position: absolute;
  top: 50%;
  right: 23px;
  transform: translateY(-50%);

  padding: 9px;

  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.header-form__search-icon {
  fill: #939393;

  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  .header-form__search-btn:hover > &,
  .header-form__search-btn:focus > & {
    fill: getColor('font-clr1');
  }
}

.header-form__select-icon {
  position: absolute;
  top: 50%;
  right: 32px;
  transform: translateY(-50%);

  fill: #939393;

  pointer-events: none;

  transition-property: fill, transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    .focusAlways>& {
      fill: getColor('font-clr1');
      transform: rotate(180deg) translateY(50%)
    }

}

.select {
  position: relative;
  z-index: 1;
  height: 40px;
  width: 280px;
  font-size: 16px;
  line-height: 1.25;
    
  color: #939393;
    
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  border: 1px solid transparent;
  outline: none;
}

.focusAlways {
  border: 1px solid getColor('font-clr1');
  border-bottom: 0;
  border-radius: 20px 20px 0 0;
  box-shadow: 0px 0px 42px 4px rgba(220, 86, 197, 0.54);
}

.select__header {
  max-width: 184px;
  padding: 10px 64px 10px 32px;
  height: 40px;
  white-space: nowrap;
  overflow: hidden;
}

.select__body {
  display: none;

  position: absolute;
  top: 100%;
  left: 0;
  z-index: 100;

  width: 280px;

  background-color: rgba(220, 86, 197, 0.9);
  border-radius: 0 0 20px 20px;
  overflow: hidden;
  
  transition-property: border;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:focus{
    border: 1px solid getColor('font-clr1');
    border-top: 0;
    left: -1;
  }

  .select:focus>& {        
    border: 1px solid getColor('font-clr1');
    border-top: 0;
    left: -1;
  }
}

.select__body.is-active {
  display: block;
}

.select__item {
  cursor: pointer;
  padding: 2px 32px;
  color: getColor('font-clr2');

  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: #939393;        
    border: none;
    outline: none;
  }
}

@include tablet {
  .page-header__thumb {
    position: relative;
    width: 768px;
    padding-top: 40px;
    padding-bottom: 170px;
    margin-left: auto;
    margin-right: auto;
  }

  .background-image__top {
    width: 179px;
    height: 151px;
    border-radius: 0 0 100% 0;
  }

  .logo {
    top: 23px;
    left: 32px;
    width: 100px;
    height: 29px;
  }

  .page-title {
    max-width: 400px;
    margin-bottom: 40px;
  }

  .header-form {
    flex-direction: row;
    gap: 32px;
  }

  .header-form__input {
    width: 336px;
    max-width: none;
  }

  .select {
    width: 336px;
  }

  .select__header {
    max-width: 240px;
  }

  .select__body {
    width: 336px;
  }

}

@include desktop {
  .page-header__thumb {
    padding-bottom: 196px;
    width: 1280px;
  }

  .background-image__top {
    width: 229px;
    height: 193px;
    border-radius: 0 0 100% 0;
  }

  .logo {
    top: 40px;
    left: 16px;
    width: 136px;
    height: 70px;
  }

  .page-title {
    margin-bottom: 48px;
    font-size: 50px;
    line-height: 1.22;

    max-width: 608px;
  }

  .header-form__input {
    width: 288px;
  }

  .select {
    width: 288px;
  }

  .select__header {
    max-width: 215px;
  }

  .select__body {
    width: 288px;
  }
}
